import React, {useState} from 'react';
import axios from 'axios';

const AdminPage = () => {
  const [day, setDay] = useState('');
  const [id, setId] = useState('');
  const [military, setMilitary] = useState('');
  const [civil, setCivil] = useState('');
  const [russian, setRussian] = useState('');
  const [rusIsolation, setRusIsolation] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const data = {
      day: Number(day),
      id: Number(id),
      military,
      civil,
      russian,
      rusIsolation,
    };
    try {
      const response = await axios.post('http://localhost:3000/api/data', data);
      console.log(response.data);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <label>
                День:
        <input type="number" value={day} onChange={(e) => setDay(e.target.value)} />
      </label>
      <label>
                Id стану:
        <input type="number" value={id} onChange={(e) => setId(e.target.value)} />
      </label>
      <label>
                Військові:
        <input type="text" value={military} onChange={(e) => setMilitary(e.target.value)} />
      </label>
      <label>
                Цивільні:
        <input type="text" value={civil} onChange={(e) => setCivil(e.target.value)} />
      </label>
      <label>
                Вороги:
        <input type="text" value={russian} onChange={(e) => setRussian(e.target.value)} />
      </label>
      <label>
                Русоізоляція:
        <input type="checkbox" checked={rusIsolation} onChange={(e) => setRusIsolation(e.target.checked)} />
      </label>
      <button type="submit">Submit</button>
    </form>
  );
};

export default AdminPage;
